nav {
    a {
        &:hover {
            background: grey;
        }
        &:last-child {
            float: right;
        }
    }
}
h1 {
    color: $main-color;
}
ul {
    color: $main-color;
    li {
        &:hover {
            color: $li-color;
        }
        &:first-child {
            &:hover {
                color: $li-firstChild;
            }
        }
    }
}
